.hero{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 10%;
    background-color: var(--color-main);
}
.text{
    height: 50%;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
}


.text_welcome{
    display: flex;
    align-items: center;
}

.text_welcome i {
    font-size: 3.8em;
    color: var(--color-orange);
}

.text span{
    color: var(--color-orange);
    font-size: 4.5em;
    font-weight: bold;
}

.fa-heart{
    margin-left: 30%;
}

.text h2, .about h2{
    font-size: 4rem;
    color: var(--color-second);
}

.span_name{
    font-size: 3.5rem !important;
}

.arrow{
    font-size: 5rem;
    color: var(--color-orange);
}


@media all and (max-width:991.98px){
    .text_welcome i {
        font-size: 4em;
    }
    
    .text span{
        font-size: 4.4em;  
    }

    .text h2{
        font-size: 3.8rem;    
    }
    
    .span_name{
        font-size: 3.5rem !important;
    }



}
@media screen and (max-width:575.98px){

    .text{
        width: 100%;
    }
    
    .text_welcome i {
        font-size: 2.2em;
    }
    
    .text span{
        font-size: 2.8em;  
    }
    
    .text h2{
        font-size: 2.2rem;
        padding-bottom: 5%;    
    }

    .span_name{
        font-size: 2.2rem !important;
    }
    
    .arrow{
        font-size: 2.2rem;
    }

}