.tech_items figure{
    flex-basis: 10%;
}

img{
    width: 95px;
    height: 80px;
}

@media all and (max-width:991.98px){

    .tech_items figure{
        flex-basis: 20%;
    }

    img{
        width: 80px;
        height: 80px;
    }
    



}
@media screen and (max-width:575.98px){

    .tech_items figure{
        flex-basis: 10%;
}

    img{
        width: 64px;
        height: 64px;

}
}