
.card {
    box-shadow: 0px 0px 24px 2px rgba(0,0,0,0.39);
    transition: 0.3s;
    text-align: center;
    width: 35%;
    height: 60vh !important;
    background-color: #1A2238 !important;
    border-radius: 20px !important;
}

.card-body{
    height: 30% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.card:hover {
    box-shadow: 0px 0px 24px 2px rgba(0,0,0,0.49);
}

.card-img-top{
    width: 100%;
    height: 60%;
    border-radius: 20px 20px 0 0 !important;
}

@media all and (max-width:991.98px){
    
    .card {
        width: 100%;
        height: 65vh;
    }
    
    .card-body{
        
        flex-direction: column;
        height: 30vh;
        font-size: 1.5em;
    }

    .card-body btn{
        font-size: 0.8em;
    }
}

@media screen and (max-width:575.98px){

    .card-body{
        font-size: 1rem;
    }
}