@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

html{
  scroll-behavior: smooth;
  --color-orange: #FF6A3D;
  --color-main: #1A2238;
  --color-second: #9DAAF2;
} 

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #ffff;
  font-family: 'Roboto', sans-serif;
  list-style: none;
  text-decoration: none;
}

p{
  letter-spacing: 1px;
}


.text h2, .about h2{
  font-size: 4rem;
  color: #9DAAF2;

}

.tech h2, .card_section h2, .contact h2{
  font-size: 4rem;
  text-align: center;
  padding-top: 10%;
  padding-bottom: 5%;
  color: #9DAAF2;
}

.tech{
  margin-top: 10%;
  padding: 0 10%;
  background-color: #1A2238;
}
.tech_items{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 10%;
}

.card-list h2{
  font-size: 4rem;
  text-align: center;
  padding-top: 10%;
  padding-bottom: 5%;
  color: #9DAAF2;
}

.card-list h3{
  color: #1A2238;
  text-align: center;
}

.card-items{
  display: flex;
  width: 100%;
  height: 150vh;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap:wrap;
  padding: 0 10%;
}

footer{
  position: relative;
  bottom: 0;
  width: 100%;
  left: 0;
  margin-top: 10%;
}

.ShowAll{
  width: 100%;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ShowAll .btn{
  padding: 20px 60px ;
}

@media all and (max-width:991.98px){
  .tech h2{
    font-size: 3.8rem;
    padding-bottom: 15%;
}

  .tech_items{
    text-align: center;
  }

  .card-list h2{
    font-size: 3.8rem;
    padding-bottom: 15%;
  }
  .card-items{
    flex-direction: column;
    height: 300vh;
    padding: 10% 10%;
  }
}

@media screen and (max-width:575.98px){

  .tech h2{
    font-size: 2.2rem;
    padding-bottom: 15%;
}


  .card-list h2{
    font-size: 2.2rem;
    padding-bottom: 15%;
}

}