.navbar{
    background-color: var(--color-main);
    padding: 30px 10%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
}
.navbar-toggler{
   border-color: var(--color-orange);
}

.navbar-collapse{
    text-align: center;
}

.nav-link{
    transition: all 0.3s ease 0s;
    color: #ffff;
    font-weight: 500;
    font-size: 1.1em;
}

.nav-link{
    padding: 0px 30px !important;
}


.nav-link:hover{
    color: var(--color-orange);
}

.btn{
    padding: 9px 25px;
    background-color: var(--color-orange);
    color: #ffff;
    border:none;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.btn:hover{
    background-color: #fff;
    color: var(--color-orange);
    border:2px solid var(--color-orange);
}