.contact{
    width: 100%;
    background-color: #1A2238;
    height: 60vh;
}

.contact_list{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 10%;
    padding-bottom: 5%;
    font-size: 3.5rem;
}

i{
color: #FF6A3D;
}


@media all and (max-width:991.98px){

    .contact h2{
        font-size: 3.8rem;
        padding-bottom: 15%;
    }

    .contact{
        height: 45vh;
    }

}
@media screen and (max-width:575.98px){

    .contact h2{
        font-size: 2.2rem;
        padding-bottom: 15%;
    }


    .contact{
        height: 40vh;
    }

    .contact_list i {
        font-size: 2.5rem;
    }
}