@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{
  scroll-behavior: smooth;
  --color-orange: #FF6A3D;
  --color-main: #1A2238;
  --color-second: #9DAAF2;
} 

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color: #ffff;
  font-family: 'Roboto', sans-serif;
  list-style: none;
  text-decoration: none;
}

p{
  letter-spacing: 1px;
}


.text h2, .about h2{
  font-size: 4rem;
  color: #9DAAF2;

}

.tech h2, .card_section h2, .contact h2{
  font-size: 4rem;
  text-align: center;
  padding-top: 10%;
  padding-bottom: 5%;
  color: #9DAAF2;
}

.tech{
  margin-top: 10%;
  padding: 0 10%;
  background-color: #1A2238;
}
.tech_items{
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  padding-bottom: 10%;
}

.card-list h2{
  font-size: 4rem;
  text-align: center;
  padding-top: 10%;
  padding-bottom: 5%;
  color: #9DAAF2;
}

.card-list h3{
  color: #1A2238;
  text-align: center;
}

.card-items{
  display: flex;
  width: 100%;
  height: 150vh;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap:wrap;
  padding: 0 10%;
}

footer{
  position: relative;
  bottom: 0;
  width: 100%;
  left: 0;
  margin-top: 10%;
}

.ShowAll{
  width: 100%;
  height: 25vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.ShowAll .btn{
  padding: 20px 60px ;
}

@media all and (max-width:991.98px){
  .tech h2{
    font-size: 3.8rem;
    padding-bottom: 15%;
}

  .tech_items{
    text-align: center;
  }

  .card-list h2{
    font-size: 3.8rem;
    padding-bottom: 15%;
  }
  .card-items{
    flex-direction: column;
    height: 300vh;
    padding: 10% 10%;
  }
}

@media screen and (max-width:575.98px){

  .tech h2{
    font-size: 2.2rem;
    padding-bottom: 15%;
}


  .card-list h2{
    font-size: 2.2rem;
    padding-bottom: 15%;
}

}

.card {
    box-shadow: 0px 0px 24px 2px rgba(0,0,0,0.39);
    transition: 0.3s;
    text-align: center;
    width: 35%;
    height: 60vh !important;
    background-color: #1A2238 !important;
    border-radius: 20px !important;
}

.card-body{
    height: 30% !important;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.card:hover {
    box-shadow: 0px 0px 24px 2px rgba(0,0,0,0.49);
}

.card-img-top{
    width: 100%;
    height: 60%;
    border-radius: 20px 20px 0 0 !important;
}

@media all and (max-width:991.98px){
    
    .card {
        width: 100%;
        height: 65vh;
    }
    
    .card-body{
        
        flex-direction: column;
        height: 30vh;
        font-size: 1.5em;
    }

    .card-body btn{
        font-size: 0.8em;
    }
}

@media screen and (max-width:575.98px){

    .card-body{
        font-size: 1rem;
    }
}
.navbar{
    background-color: var(--color-main);
    padding: 30px 10%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 1;
    width: 100%;
}
.navbar-toggler{
   border-color: var(--color-orange);
}

.navbar-collapse{
    text-align: center;
}

.nav-link{
    transition: all 0.3s ease 0s;
    color: #ffff;
    font-weight: 500;
    font-size: 1.1em;
}

.nav-link{
    padding: 0px 30px !important;
}


.nav-link:hover{
    color: var(--color-orange);
}

.btn{
    padding: 9px 25px;
    background-color: var(--color-orange);
    color: #ffff;
    border:none;
    border-radius: 50px;
    cursor: pointer;
    transition: all 0.3s ease 0s;
}

.btn:hover{
    background-color: #fff;
    color: var(--color-orange);
    border:2px solid var(--color-orange);
}
.contact{
    width: 100%;
    background-color: #1A2238;
    height: 60vh;
}

.contact_list{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 0 10%;
    padding-bottom: 5%;
    font-size: 3.5rem;
}

i{
color: #FF6A3D;
}


@media all and (max-width:991.98px){

    .contact h2{
        font-size: 3.8rem;
        padding-bottom: 15%;
    }

    .contact{
        height: 45vh;
    }

}
@media screen and (max-width:575.98px){

    .contact h2{
        font-size: 2.2rem;
        padding-bottom: 15%;
    }


    .contact{
        height: 40vh;
    }

    .contact_list i {
        font-size: 2.5rem;
    }
}
.about{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70vh;
    padding: 10%  10% 0 ;
}

.about_text{
    width: 40%;
}

.about p{
    color: #1A2238;;
    padding-top: 10%;
    font-size: 1.5em;
    font-weight: 500;
}

.about_img{
    background-image: url('https://images.unsplash.com/photo-1569144157581-984dea473e3b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: 100%;
    width: 50%;
}

@media all and (max-width:991.98px){

    .about h2{
        font-size: 3.8rem;    
    }

    
    .about{  
        flex-direction: column;
        width: 100%;
        height: 100vh;
        padding: 10%  10% 10%; 
    }

    .about_text{
        width: 100%;
        text-align: center;
    }


    .about p{
        font-size: 1.8em;
        padding-bottom: 5%;

    }

    .about_img{
        width: 100%;
    }
}
@media screen and (max-width:575.98px){

    .about h2{
        font-size: 2.2rem !important;
        padding-bottom: 5%;
    }
    .arrow{
        font-size: 2.2rem;
    }
    
    .about p{
        font-size: 1rem;
    }
}


.hero{
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 10%;
    background-color: var(--color-main);
}
.text{
    height: 50%;
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    text-align: center;
}


.text_welcome{
    display: flex;
    align-items: center;
}

.text_welcome i {
    font-size: 3.8em;
    color: var(--color-orange);
}

.text span{
    color: var(--color-orange);
    font-size: 4.5em;
    font-weight: bold;
}

.fa-heart{
    margin-left: 30%;
}

.text h2, .about h2{
    font-size: 4rem;
    color: var(--color-second);
}

.span_name{
    font-size: 3.5rem !important;
}

.arrow{
    font-size: 5rem;
    color: var(--color-orange);
}


@media all and (max-width:991.98px){
    .text_welcome i {
        font-size: 4em;
    }
    
    .text span{
        font-size: 4.4em;  
    }

    .text h2{
        font-size: 3.8rem;    
    }
    
    .span_name{
        font-size: 3.5rem !important;
    }



}
@media screen and (max-width:575.98px){

    .text{
        width: 100%;
    }
    
    .text_welcome i {
        font-size: 2.2em;
    }
    
    .text span{
        font-size: 2.8em;  
    }
    
    .text h2{
        font-size: 2.2rem;
        padding-bottom: 5%;    
    }

    .span_name{
        font-size: 2.2rem !important;
    }
    
    .arrow{
        font-size: 2.2rem;
    }

}
.tech_items figure{
    flex-basis: 10%;
}

img{
    width: 95px;
    height: 80px;
}

@media all and (max-width:991.98px){

    .tech_items figure{
        flex-basis: 20%;
    }

    img{
        width: 80px;
        height: 80px;
    }
    



}
@media screen and (max-width:575.98px){

    .tech_items figure{
        flex-basis: 10%;
}

    img{
        width: 64px;
        height: 64px;

}
}
