.about{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 70vh;
    padding: 10%  10% 0 ;
}

.about_text{
    width: 40%;
}

.about p{
    color: #1A2238;;
    padding-top: 10%;
    font-size: 1.5em;
    font-weight: 500;
}

.about_img{
    background-image: url('https://images.unsplash.com/photo-1569144157581-984dea473e3b?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=687&q=80');
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    height: 100%;
    width: 50%;
}

@media all and (max-width:991.98px){

    .about h2{
        font-size: 3.8rem;    
    }

    
    .about{  
        flex-direction: column;
        width: 100%;
        height: 100vh;
        padding: 10%  10% 10%; 
    }

    .about_text{
        width: 100%;
        text-align: center;
    }


    .about p{
        font-size: 1.8em;
        padding-bottom: 5%;

    }

    .about_img{
        width: 100%;
    }
}
@media screen and (max-width:575.98px){

    .about h2{
        font-size: 2.2rem !important;
        padding-bottom: 5%;
    }
    .arrow{
        font-size: 2.2rem;
    }
    
    .about p{
        font-size: 1rem;
    }
}

